.Container_after {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.Set_after {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  width: 100vw;
}

.Footer_after {
  margin-bottom: 0px;
}

.Msg_after {
  margin-top: 15px;
  background-color: white;
  width: 90vw;
  height: 100px;
  border: 0;
  border-top-right-radius: 30px;
  border-top-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.Text_after {
  font-family: "Jalnan";
  color: white;
  font-size: 5px;
  border: 0px;
  margin-left: 10px;
}

.Text2_after {
  font-family: "Jalnan";
  color: #f05052;
  font-size: 20px;
}
